* {
    box-sizing: border-box;
}
html, body, div, img {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
body {
    line-height: 1;
}
.products {
    display : flex; 
    flex-wrap: wrap;
   padding: 5px;
 
   

}

.item {
margin: 5px;

}


@media (min-width: 1820px) {
    .products {
      margin: 0 400px;
    }
  }
  
  @media (min-width: 1620px) {
    .products {
      margin: 0 300px;
    }
  }
  
  @media (min-width: 1366px) {
    .products {
      margin: 0 200px;
    }
  }
